

body {
    font-size: 17px;
}

a {
    @apply .font-semibold;
    @apply .no-underline;
    @apply .text-blue-600;

    &:hover {
        @apply .text-blue-800;
    }
}

blockquote {
    @apply .border-blue-400;
    @apply .border-l-4;
    @apply .font-normal;
    @apply .italic;
    @apply .my-8;
    @apply .pl-6;
    @apply .text-gray-800;
    @apply .text-lg;
}

//Tomorrow Dark
.hljs-comment, pre .comment, pre .title {
    color: #969896;
}

.hljs-red, pre .variable, pre .attribute, pre .tag, pre .regexp, pre .ruby .constant, pre .xml .tag .title, pre .xml .pi, pre .xml .doctype, pre .html .doctype, pre .css .id, pre .css .class, pre .css .pseudo {
    color: #cc6666;
}

.hljs-orange, pre .number, pre .preprocessor, pre .built_in, pre .literal, pre .params, pre .constant {
    color: #de935f;
}

.hljs-yellow, pre .class, pre .ruby .class .title, pre .css .rules .attribute {
    color: #f0c674;
}

.hljs-green, pre .string, pre .value, pre .inheritance, pre .header, pre .ruby .symbol, pre .xml .cdata {
    color: #b5bd68;
}

.hljs-aqua, pre .css .hexcolor {
    color: #8abeb7;
}

.hljs-blue, pre .function, pre .python .decorator, pre .python .title, pre .ruby .function .title, pre .ruby .title .keyword, pre .perl .sub, pre .javascript .title, pre .coffeescript .title {
    color: #81a2be;
}

.hljs-purple, pre .keyword, pre .javascript .function {
    color: #b294bb;
}

pre code {
    display: block;
    background: #1d1f21;
    color: #c5c8c6;
    font-family: Menlo, Monaco, Consolas, monospace;
    line-height: 1.5;
    border: 1px solid #ccc;
    padding: 10px;
}



